<template lang="pug">
.px-1

  //- b-alert(show)     

  b-card(no-body)
    b-card-header
      b-input-group
        b-input-group-prepend(is-text) 
        b-form-input.nokeyboard#input-wpi(v-model="wpi" placeholder=" C" @blur="testScanerState = null" autofocus :state="testScanerState" @keyup="testScaner")
        b-form-valid-feedback#input-wpi  ,   !
        b-form-invalid-feedback#input-wpi     ENTER

    b-card-footer   
      b-input-group
        b-input-group-prepend(is-text) 
        b-form-input.nokeyboard#input-weight(v-model="weight" placeholder=" " @blur="testWeightState = null" :state="testWeightState" @keyup="testWeight")
        b-form-valid-feedback#input-weight  ,   !
        b-form-invalid-feedback#input-weight     ENTER

  b-card(no-body).mt-2
    b-card-body
      Yarlik(:response="testYarlik")
    b-card-footer.text-center
      b-btn(@click="testPrint" variant="outline-primary")  

  b-card(no-body).mt-2
    b-card-body
      b-input-group
        b-input-group-prepend(is-text)  LED 
        b-input-group-append
          b-btn(@click="testLedRoll" variant="success") 
        b-input-group-append
          b-btn(@click="testLedError" variant="danger") 
        b-input-group-append
          b-btn(@click="testLedOff" variant="secondary") 
          

  
    
</template>

<script>
import Vue from 'vue'
import Yarlik from '@/components/YarlikPrintTemplate'
import {$leds} from '@/store/api/http'

var captureTM = null;

export default {
  name: 'DiagnosticModal',
  data () {
    return {
      wpi:null,
      weight:null,
      testYarlik: {
          "result": "success",
          "labelListNo": "G2020555"+Math.ceil(Math.random()*100000),
          "actualWeight": 1050,
          "workerName": " ",
          "date": new Date().toString(),
          "count": 50,
          "packetListCount":50,
          "toTechindex": "050009",
          "toDepartment": "-9",
          "totalWeight": 9999,
          "fromTechindex": "055990",
          "techindex": "055990",
          "fromDepartment": "   ",
          "route": "",
          "sendMethod": 1,
          "bagType": 1,
          "taraType": 1,
          "plombaNum": "1234567890123",
          "comment":'DEMO:   '
      },
      led:0,
      testScanerState:null,
      testWeightState:null,
    }
  },
  methods:{
    testScaner(){
      if (event.keyCode == 13 && this.wpi != "") this.testScanerState = true
      else if(this.wpi != "") this.testScanerState = false
    },
    testWeight(){
      if (event.keyCode == 13 && this.weight != "") this.testWeightState = true
      else if(this.weight != "") this.testWeightState = false
    },
    testPrint(){ window.print(); },
    testLedRoll(){
      
      this.testLedOff();
      captureTM = window.setInterval(()=>{
        $leds.push(this.led++);
      },5000)
    },
    testLedError(){
      this.testLedOff();
      $leds.error();
    },
    testLedOff(){
      this.led = 0;
      window.clearInterval(captureTM);
      $leds.$ledoff();
    },
  },
  components:{
    Yarlik
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus">

  
.demoprint
  .card
    flex 0 0 auto !important
    margin 0.1rem !important
    
    .barcode
      font-size 22px !important
      
</style>
<style lang="stylus">

@media print
    
  .demoprint
    .card
      flex 0 0 auto !important
      width 15rem !important
      
      .barcode 
        padding 0.5rem
        font-size 1.5rem !important
      
    .users .card      
      width 18rem !important
      
      
      
            
</style>



